import { newE2EPage } from '@stencil/core/testing';

describe('presto-timestamp', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-timestamp></presto-timestamp>');

    const element = await page.find('presto-timestamp');
    expect(element).toHaveClass('hydrated');
  });
});
